.grad_underline {
  background: linear-gradient(to left, transparent, transparent), linear-gradient(to left, var(--background-selected), var(--font-color-active));
  background-size: 100% 1px, 0 1px;
  background-position: 100% 100%, 0 100%;
  background-repeat: no-repeat;
  transition: background-size 400ms;
}

.gradient-box {
  background: linear-gradient(to right, rgba(11, 72, 107, 0.7411764706), #0b486b, rgba(11, 72, 107, 0.7411764706));
}

.gradient-top-link {
  text-shadow: #f67438 1px 0 10px;
  background: linear-gradient(to bottom, #008192, #90effc, #008192 100%);
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient, .main-content__spacer, .main-content__slide {
  background: linear-gradient(to left, #0b0813, #392b7c 50%, #0c0814 100%);
}

.liner-gradient-box {
  background: linear-gradient(45deg, #10133a 55%, rgba(71, 32, 138, 0.4509803922) 10%, #10133a);
}

.text-gradient {
  background: currentColor 100%;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient-header, .header::before {
  background: linear-gradient(-45deg, #1a1530, #332764 60%, rgb(42, 53, 20) 10%, #000 70%);
}

.gradient-services, .main-content__services {
  background: linear-gradient(to right, #0b0813 20%, #1c153f 50%, #0b0813 80%);
}

.gradient-spec, .offer-container__column {
  background: linear-gradient(-45deg, #fe59c2, #170e31 83%);
  background-size: 200% 200%;
}

.gradient-title, .offer-container__title::before, .services__title::before {
  background: linear-gradient(to top, #170e31 10%, #402e6b 50%, #1a1530 100%);
}

.gradient-spec-active, .offer-container__column:nth-child(2):hover, .offer-container__column:hover {
  background: linear-gradient(-45deg, #04d9ff, #170e31 83%);
  background-size: 200% 200%;
}

.gradient-spec-comfort, .offer-container__column:nth-child(2) {
  background: linear-gradient(-45deg, #c8ff00, #170e31 83%);
  background-size: 200% 200%;
}

.gradient-border-right, .main-content__slide::after {
  background: linear-gradient(to right, rgba(42, 53, 20, 0.685) 20%, #332764 50%, #2a3514 80%);
}

.gradient-border-left {
  background: linear-gradient(to left top, #8164ff 40%, rgb(42, 53, 20) 70%);
}

.icon-style {
  border: 1px var(--border-color);
  border-style: none solid;
  height: 100%;
  padding: 0 1.5rem;
  display: flex;
  align-items: center;
}

.shadow-after, .offer-container__title::after, .services__title:nth-child(1)::after {
  transform: rotateX(15deg) translateZ(-10px);
  color: rgba(23, 14, 49, 0.479);
  text-shadow: 5px 0px 5px rgb(23, 14, 49), 0 0 40px rgb(4, 217, 255), 0px 0px 20px rgb(255, 7, 58);
  z-index: -1;
}

.overflow-hidden {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
}
.overflow-hidden::-webkit-scrollbar {
  height: 0;
}

@media (min-width: 920.89px) {
  /* FLOW: RIGHT */
  [data-tooltip] {
    position: relative;
  }
  [data-tooltip]::after {
    content: attr(data-tooltip);
    border-radius: 0.6rem;
    border: 1px solid var(--border-color);
    background-color: rgba(35, 52, 59, 0.8705882353);
    position: absolute;
    padding: 1.2rem;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
    font-family: "Roboto", sans-serif;
    font-weight: 200;
    font-size: 1.3rem;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    z-index: 9;
    white-space: nowrap;
  }
  [data-tooltip]::before {
    content: "";
    border: 9px solid transparent;
    border-right: 9px solid var(--font-color-active);
    position: absolute;
    z-index: 9;
    margin-left: -20px;
    bottom: 12%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
  }
  [data-tooltip]:hover::before,
  [data-tooltip]:hover::after {
    visibility: visible;
    opacity: 1;
    display: block;
  }
  [data-tooltip=""]::before,
  [data-tooltip=""]::after {
    display: none !important;
  }
  [data-tooltip][flow^=right]::after {
    top: 50%;
    left: calc(120rem + 10px);
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]::before {
    top: 50%;
    left: 121rem;
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]:hover::before,
  [data-tooltip][flow^=right]:hover::after {
    animation: data-tooltip 300ms ease-out forwards;
  }
  @keyframes data-tooltip {
    to {
      opacity: 1;
      transform: translate(-110rem, -50%);
    }
  }
}
.breadcrumb a:after {
  content: "";
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  border-left: 12px solid #e7e9eb;
  position: absolute;
  right: -12px;
  top: 0;
  z-index: 1;
}

html {
  font-style: normal;
  font-size: 62.5%;
  font-weight: 470;
  font-family: "Roboto", sans-serif;
  height: 100%;
  counter-reset: heading;
  color: var(--font-body-color);
  letter-spacing: 1.7px;
}
@media (max-width: 1024px) {
  html {
    font-size: 50%;
  }
}

body {
  overscroll-behavior: none;
  font: 1.6rem/1.6 Roboto, sans-serif;
}
@media (max-width: 1024px) {
  body {
    font: 1.8rem/1.6 Roboto, sans-serif;
  }
}
@media (max-width: 320px) {
  body {
    font: 1.4rem/1.4 Roboto, sans-serif;
  }
}

* {
  padding: 0px;
  margin: 0px;
  border: none;
  box-sizing: border-box;
  scrollbar-width: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  vertical-align: top;
}

img,
svg {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
}

a:hover {
  text-decoration: none;
}

a:visited {
  vertical-align: middle;
  text-emphasis-style: none;
}

input,
button,
textarea {
  border: none;
}

button,
input[type=submit] {
  display: inline-block;
  box-shadow: none;
  background-color: transparent;
  background: none;
  cursor: pointer;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
  outline: none;
}

input,
textarea,
button,
select {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  background-color: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

label {
  cursor: pointer;
}

a:focus,
a:active,
input:focus,
textarea:focus {
  outline: none;
}

ul,
ol,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-style: inherit;
}

::selection {
  color: #9b9999;
  background-color: rgba(0, 84, 99, 0.2470588235);
  text-shadow: 0 1px 0 #000;
}

::-moz-selection {
  color: #9b9999;
  background-color: rgba(0, 84, 99, 0.2470588235);
  text-shadow: 0 1px 0 #000;
}

::-webkit-scrollbar {
  width: 0.5rem;
  padding: 0.031em;
}

::-webkit-scrollbar-track {
  background: var(--scrollbar-thumb-bgcolor);
}

::-webkit-scrollbar-thumb {
  border: 1px solid var(--border-color);
  border-radius: 2px;
  background-color: var(--themes-color);
  background-image: -webkit-linear-gradient(45deg, var(--background-thumb) 25%, transparent 25%, transparent 50%, var(--background-thumb) 50%, var(--background-thumb) 75%, transparent 75%, transparent);
}

::-webkit-scrollbar-thumb:hover {
  background-color: var(--background-thumb);
  background-image: -webkit-linear-gradient(45deg, var(--themes-color) 25%, transparent 25%, transparent 50%, var(--themes-color) 50%, var(--themes-color) 75%, transparent 75%, transparent);
}

